.Productlist {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 50px 100px;
  justify-content: center;
}
.Productlist button {
  padding: 8px;
}
.Productlist-header {
  font-weight: 600;
  font-size: 50px;
}


.Productlist-desc {
  margin: 20px 0;
}

.Productlist-list {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

@media only screen and (max-width: 900px) {
  .Productlist-header {
    margin-top: 2rem;
    font-weight: 500;
    font-size: 40px;
  }

  .Productlist {
    padding: 50px 50px;
  }
}

@media only screen and (max-width: 600px) {
  .Productlist-header {
    margin-top: 2rem;
    font-weight: 500;
    font-size: 30px;
  }
  
  .Productlist {
    padding: 50px 20px;
  }
}
