.nav-bar {
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  background-color: rgb(255, 255, 255);
  z-index: 1212;
}

.uper-nav {
  display: flex;
  justify-content: space-between;
  position: fixed;
  padding: 10px 20px;
  top: 0;
  right: 0;
  width: 100%;
  z-index: 5;
}

.logo {
  font-weight: 600;
  color: #1c92d2;
}

.uper-nav ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.uper-nav ul li {
  list-style: none;
  padding: 0 7px;
}

.uper-nav ul li a {
  color: rgb(0, 0, 0);
}

.toggle {
  display: flex;
  text-align: center;
}

.toggle i {
  font-size: 30px;
  margin-left: 10px;
  cursor: pointer;
}

.ul-nav {
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  top: 0;
  right: -110%;
  width: 100%;
  height: 100%;
  z-index: 2;
  background: #1c92d2; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #f2fcfe,
    #1c92d2
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #f2fcfe,
    #1c92d2
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  transition: 0.3s ease;
}

.ul-nav.active {
  right: 0;
}

.ul-nav ul {
  display: flex;
  position: fixed;

  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.ul-nav ul li a {
  color: transparent;
  letter-spacing: 3px;
  font-size: 80px;
  cursor: pointer;
  user-select: none;
  font-weight: bolder;
  color: white;
}

/* media for nav */

@media only screen and (max-width: 600px) {
  .toggle i {
    font-size: 25px;
    margin-left: 10px;
    cursor: pointer;
  }

  .ul-nav ul li a {
    letter-spacing: 3px;
    font-size: 60px;
    font-weight: bolder;
  }
}
