.fotter {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px 0;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}
.menui {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 7;
}

.menui .img {
  height: 100px;
}

li {
  padding: 0 10px;
  list-style: none;
}

