.background {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.background img {
  width: 10px;
  position: absolute;
  max-width: 50px;
  max-height: 50px;
  width: 100%;
}

.background img:nth-child(1) {
  top: 250px;
  left: 30px;
}
.background img:nth-child(2) {
  top: 400px;
  right: 50px;
}
/* js */
.background img:nth-child(3) {
  top: 400px;
  left: 30px;
}
/* //html */
.background img:nth-child(4) {
  top: 200px;
  right: 70px;
}
.background img:nth-child(5) {
  top: 500px;
  right: 400px;
}

.background img:nth-child(6) {
  top: 200px;
  left: 70px;
}

@media only screen and (max-width: 800px) {
  .background {
    display: none;
  }
}

