.about {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

.about-left {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.card {
  position: relative;
  width: 45%;
  height: 70vh;
  border-radius: 30px;
  overflow: hidden;
}

.card.bg {
  position: absolute;
  top: 50px;
  left: 50px;
  background-color: rgb(53, 51, 51);
}

.card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about-right {
  flex: 1;
}

.about-right span {
  color: #1c92d2;
  font-weight: bold;
}

.about-title {
  margin-bottom: 10px;
}

.about-desc ul {
  margin-top: 10px;
}

.about-desc ul {
  list-style: none;
}

@media only screen and (max-width: 1200px) {
  .card.bg {
    display: none;
  }
  .card {
    position: relative;
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    overflow: hidden;
  }
}

@media only screen and (max-width: 800px) {
  .about-left {
    display: none;
  }

  .about {
    padding: 0 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 100vh;
  }
}
